<template>
  <div class="container">
    <van-cell v-for="(item, index) in list" :key="item.en">
      <div class="c-word">
        {{ index + 1 }}.
        <template v-for="key in setting.keys">
          <span :key="key" v-if="setting.show.includes(key)">
            {{ key === "pronunciation" ? `/${item[key]}/` : item[key] }}
          </span>
        </template>
        <span class="phraseSpeech">
          <a
            title="点击发音"
            href="javascript:;"
            class="pronounce"
            @click="onClick(item.en)"
          ></a>
        </span>
      </div>
    </van-cell>

    <!-- 设置 -->
    <div class="icon-setting" @click="show = true">
      <Icon name="setting" />
    </div>

    <!-- 设置面板 -->
    <ActionSheet v-model="show" title="设置">
      <div class="action-content">
        <van-form>
          <van-field label="显示">
            <template #input>
              <van-checkbox-group v-model="setting.show" direction="horizontal">
                <van-checkbox name="en" shape="square">英文</van-checkbox>
                <van-checkbox name="pronunciation" shape="square"
                  >音标</van-checkbox
                >
                <van-checkbox name="zh" shape="square">中文</van-checkbox>
              </van-checkbox-group>
            </template>
          </van-field>
        </van-form>
      </div>
    </ActionSheet>

    <!-- 分页 -->
    <Pagination
      class="pagination"
      v-model="currentPage"
      :page-count="21"
      :show-page-size="5"
      force-ellipses
      @change="onChange"
    />
  </div>
</template>

<script>
import {
  Icon,
  Pagination,
  ActionSheet,
  Checkbox,
  CheckboxGroup,
  Cell,
} from "vant";

export default {
  components: {
    Icon,
    ActionSheet,
    Pagination,
    [Cell.name]: Cell,
    [Checkbox.name]: Checkbox,
    [CheckboxGroup.name]: CheckboxGroup,
  },
  data() {
    return {
      show: false,
      currentPage: 1,
      list: [],
      setting: {
        keys: ["en", "pronunciation", "zh"],
        show: ["en", "pronunciation", "zh"],
      },
    };
  },
  async created() {
    this.getList(1);
  },
  methods: {
    getList(p) {
      this.list = require(`@/json/${p}.json`);
      document.body.scrollTop = document.documentElement.scrollTop = 0;
    },
    onClick(word) {
      var player = new Audio(
        `https://dict.youdao.com/dictvoice?audio=${word}&type=1`
      );
      player.play();
    },
    onChange(val) {
      this.getList(val);
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  padding-bottom: 80px;
}
.c-word {
  span + span {
    margin-left: 5px;
  }
}
.icon-setting {
  width: 40px;
  height: 40px;
  display: flex;
  background: #000;
  opacity: 0.3;
  border-radius: 10px;
  position: relative;
  z-index: 1000;
  transition: opacity 0.3s;
  color: #fff;
  font-size: 25px;
  align-items: center;
  justify-content: center;
  position: fixed;
  right: 20px;
  bottom: 20%;
}
.pagination {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
}
.phraseSpeech {
  display: inline-block;
  width: 22px;
  height: 22px;
  vertical-align: middle;
  font-size: 0;
  letter-spacing: 0;
}
.pronounce {
  display: block;
  width: 100%;
  height: 100%;
  background: url("@/assets/voice.png") 50% 50% no-repeat;
  background-size: contain;
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
}
</style>
